                var deleteItem, deletePermission;


                if (me.deleteConfirmCheckBox) {
                    if (me.selectDelete.projectType == 'mine') {
                        deleteItem = firebase.database().ref('/userLists/' + localStorage.getItem('uid') + '/' + me.selectDelete.projectType + "/" + me.selectDelete.projectId)
                    } else if (me.selectDelete.projectType == 'share') {
                        deleteItem = firebase.database().ref('/userLists/' + localStorage.getItem('uid') + '/' + me.selectDelete.projectType + '/' + me.selectDelete.projectId)
                        deletePermission = firebase.database().ref('/definitions/' + me.selectDelete.projectId + '/shareOptions/permissions/' + localStorage.getItem('uid'))
                    }


                    deleteItem.remove()
                    if (me.selectDelete.projectType == 'share') {
                        deletePermission.remove()
                    }
                    me.deleteConfirmCheckBox = false

                    me.$nextTick(function () {
                        me.getList()
                        me.deleteDialog = false
                    })

                } else {
                    alert("삭제를 원하시면 '☐' 체크 해주세요.")
                }

            },
            getUid() {
                return new Promise(function (resolve) {
                    firebase.auth().onAuthStateChanged(function (user) {
                        if (user) {
                            resolve(user.uid);
                        } else {
                            resolve("everyone")
                        }
                    });
                })
            },
            getLocalList() {
                var me = this
                var filter

                return new Promise(function (resolve) {
                    var userId = localStorage.getItem("uid")

                    var local = [
                        {
                            author: localStorage.getItem("uid") != null ? localStorage.getItem("uid") : "anonymous",
                            email: localStorage.getItem("email") != null ? localStorage.getItem("email") : "anonymous@sample.org",
                            date: localStorage.getItem("localSaveTime") != null ? localStorage.getItem("localSaveTime") : "undefined",
                            img: localStorage.getItem('localSaveImg') != null ? localStorage.getItem('localSaveImg') : 'https://github.com/sooheon45/eventstorming/blob/master/docs/media/evtstrm.jpg?raw=true',
                            userUid: userId != null ? userId : "anonymous",
                            projectId: 'init',
                            projectVersion: '-init',
                            projectName: localStorage.getItem("localSaveProjectName") != null ? localStorage.getItem("localSaveProjectName") : 'local',
                            projectType: 'local',
                        },
                    ]
                    if (JSON.parse(localStorage.getItem('localLoadData')) != undefined) {
                        if (JSON.parse(localStorage.getItem('localLoadData')).definition.length > 0) {
                            me.stormingLocalLists = local
                        } else {
                            me.stormingLocalLists = []

                        }
                    } else {
                        me.stormingLocalLists = []
                    }

                    resolve()
                })

            },
            getMineList() {
                var me = this
                var userId = localStorage.getItem("uid")
                var filter = []
                me.stormingMineKeyLists = []

                return new Promise(function (resolve) {

                    firebase.database()
                        .ref('/userLists/' + userId + '/mine/')
                        .once('value', function (snapshot) {
                            if (snapshot.exists()) {

                                Object.keys(snapshot.val()).forEach(function (proId) {
                                    var obj = {
                                        projectId: proId,
                                        projectName: ''
                                    }
                                    me.stormingMineKeyLists.push(obj)
                                })
                                // me.stormingMineKeyLists = Object.keys(snapshot.val())
                            }
                        })
                    resolve()
                })
            },
            getShareList() {
                var me = this
                var userId = localStorage.getItem("uid")
                var filter = []
                me.stormingShareKeyLists = []

                return new Promise(function (resolve) {

                    firebase.database()
                        .ref('/userLists/' + userId + '/share/')
                        .once('value', function (snapshot) {
                            if (snapshot.exists()) {
                                Object.keys(snapshot.val()).forEach(function (proId) {
                                    var obj = {
                                        projectId: proId,
                                        projectName: ''
                                    }
                                    me.stormingShareKeyLists.push(obj)
                                })

                                // me.stormingShareKeyLists = Object.keys(snapshot.val())
                            }
                        })
                    resolve()
                })
            },

            getEveryOneList() {
                var me = this
                var userId = localStorage.getItem("uid")
                var filter = []
                me.stormingEveryKeyLists = []

                return new Promise(function (resolve) {
                    firebase.database()
                        .ref('/userLists/everyone/share/')
                        .once('value', function (snapshot) {
                            if (snapshot.exists()) {
                                Object.keys(snapshot.val()).forEach(function (proId) {
                                    var obj = {
                                        projectId: proId,
                                        projectName: ''
                                    }
                                    me.stormingEveryKeyLists.push(obj)
                                })

                                // me.stormingEveryKeyLists = Object.keys(snapshot.val())
                            }
                        })
                    resolve()
                })
            },
            getList() {
                var me = this

                return new Promise(function (resolve) {

                    var userId = localStorage.getItem("uid")

                    if (window.localStorage.getItem("accessToken") != null) {
                        if (userId) {

                            me.getLocalList().then(function () {
                                me.getMineList().then(function () {
                                    me.getShareList().then(function () {
                                        me.getEveryOneList().then(function () {
                                            me.$EventBus.$emit('progressValue', false)
                                            resolve()
                                        })
                                    })
                                })
                            })


                        } else {
                            me.getLocalList().then(function () {
                                me.getEveryOneList().then(function () {
                                    me.$EventBus.$emit('progressValue', false)
                                    resolve()
                                })
                            })


                        }
                    } else {
                        me.getLocalList().then(function () {
                            me.getEveryOneList().then(function () {
                                me.$EventBus.$emit('progressValue', false)
                                resolve()
                            })
                        })


                    }

                })


            }
        }
    }
</script>

